import { Component, OnInit, ViewChild, ElementRef, ViewContainerRef, ComponentFactoryResolver, Inject } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { QuickaccessComponent, AllReportService, FileExportService } from '../../../shared';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';

@Component({
  selector: 'app-cost-of-materials',
  templateUrl: './cost-of-materials.component.html',
  styleUrls: ['./cost-of-materials.component.scss']
})
export class CostOfMaterialsComponent implements OnInit {

  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('grnComboBox') grnComboBox: jqxComboBoxComponent;
  @ViewChild('grnComboBox1') grnComboBox1: jqxComboBoxComponent;

  grnAdapter: any = [];
  grnAdapter1: any = [];

  grnNo: any;

  constructor(
    private rs: AllReportService,
    private fes: FileExportService,
    private componentFactoryResolver: ComponentFactoryResolver,
  ) {
  }

  ngOnInit() {
    this.createQuickAccess();
    this.rs.getAllGrn({ limit: 'all', local: 'true' }).subscribe(
      response => {
        this.grnAdapter = response;
      },
      error => {
        console.log(error);
      }
    )

    this.rs.getAllGrn({ limit: 'all' }).subscribe(
      response => {
        this.grnAdapter1 = response;
      },
      error => {
        console.log(error);
      }
    )

  }

  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }

  generateReport() {
    let post = {};
    post['grn_no'] = this.grnNo || null;
    if (post['grn_no']) {
      this.jqxLoader.open();
      post['limit'] = 'all';
      post['report'] = true;
      
      this.rs.getCostOfMaterials(post).subscribe(
        result => {
          console.log(result);
          this.fes.SaveFile(result, 'Cost of Materials');
          this.jqxLoader.close();
        },
        error => {
          this.jqxLoader.close();
        });
    } else {

      let messageDiv: any = document.getElementById('windowMessage');
      messageDiv.innerHTML = 'Please Select GRN Number First';
      this.winNotification.open();

    }

  }


  onSelect(event: any, type): void {
    let args = event.args;
    if (args != undefined) {
      if (type == "1") {
        this.grnComboBox1.val('');
        this.grnNo = this.grnComboBox.val();
      } else {
        this.grnComboBox.val('');
        this.grnNo = this.grnComboBox1.val();
      }
    }
  }
}



