import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CostOfMaterialsComponent } from './cost-of-materials.component';

const routes: Routes = [
    {
        path: '', component: CostOfMaterialsComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class CostOfMaterialsRoutingModule { }



