import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { CostOfMaterialsComponent } from './cost-of-materials.component';
import { CostOfMaterialsRoutingModule } from './cost-of-materials-routing.module';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    CostOfMaterialsRoutingModule
  ],
  declarations: [
    CostOfMaterialsComponent
  ],
  schemas:[
    NO_ERRORS_SCHEMA
  ]
})
export class CostOfMaterialsModule { }



